		checkHandle(index) {
			if (this.itemList[index].checked === true) {
				this.itemList[index].checked = false;
			} else {
				this.itemList[index].checked = true;
			}

		},
		check(bool) {
			for (let item of this.itemList) {
				item.checked = bool;
			}
		},
		reverseCheck() {
			for (let item of this.itemList) {
				if (item.checked === true) {
					item.checked = false;
				} else if (item.checked === false) {
					item.checked = true;
				}
			}
		}
	}
